<template>
  <div class="home">
    <Header/>
    <Content/>
    <div v-html="homeContent.html_context" />
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import axios from 'axios'
import Header from './Header'
import Content from './Content'

export default {
  name: 'Home',
  components: {
    Header,
    Content
  },
  computed: {
    ...mapGetters([
      'homeContext',
      'headerNav',
      'homeContent'
    ])
  },
  created() {
    // axios.defaults.headers.post['Content-Type'] = 'application/json';
    axios.get("https://sniweb.shouting.feedia.co/php/GetHomePage.php", {
      headers: {
        'Content-Type': 'application/json'
      }
    }).then(((res) => {
      this.actionUpdateContext(res.data);
    }));

    axios.get("https://sniweb.shouting.feedia.co/php/GetNav.php", {
      headers: {
        'Content-Type': 'application/json'
      }
    }).then(((res) => {
      this.actionHeaderNav(res.data);
    }));

    axios.post("https://sniweb.shouting.feedia.co/php/GetContent.php",
      JSON.stringify({
        "page_id": 1
      })
    ).then((response) => {
      this.actionSetContent(response.data);
    });
  },
  methods: {
    ...mapActions([
      'actionUpdateContext',
      'actionHeaderNav',
      'actionSetContent'
    ])
  }
}
</script>

