<template>
    <div class="header-wrap">
        <div class="header-title">
            <h1>{{ homeContext.web_title }}</h1>
            <p>{{ homeContext.web_sub_title }}</p>
        </div>
        <Menu mode="horizontal" :theme="theme1" active-name="1">
            <Submenu
                v-for="(item, index) in headerNav"
                :key="`menu-${index}`"
                :name="item.page_group_id"
            >
                <template slot="title">
                    <Icon type="ios-stats" />
                    {{ item.group_name }}
                </template>
                <MenuItem
                    v-for="(subItem, subIndex) in item.page_content"
                    :key="`submenu-${subIndex}`"
                    :name="`${index}-${subIndex}`"
                    @click.native="enterPage(subItem.page_content_id)"
                >
                    {{ subItem.page_name || 'test' }}
                </MenuItem>
            </Submenu>
        </Menu>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import axios from 'axios'

export default {
    data() {
        return {
            title: '生長之家',
            subTitle: '一句簡單的感謝',
            theme1: 'light'
        };
    },
    computed: {
        ...mapGetters([
            'homeContext',
            'headerNav'
        ])
    },
    methods: {
        ...mapActions([
        'actionUpdateContext',
        'actionHeaderNav',
        'actionSetContent'
        ]),
        enterPage(id) {
            axios.post("https://sniweb.shouting.feedia.co/php/GetContent.php",
                JSON.stringify({
                    "page_id": id
                })
            ).then((response) => {
                this.actionSetContent(response.data);
                this.$route.push(`/${id}`);
            });
        }
    }
}
</script>

